import styled from 'styled-components';
import movieDB from '../../components/utility/movieDB';

const Styled = styled.section``;

const MovieProfile = ({ movie, typeFor }) => {
  console.log('profile', movie);
  return <Styled className='profile'>Moviess fsdafa asfasfa</Styled>;
};

MovieProfile.getInitialProps = async ({ asPath }) => {
  try {
    const movie = await movieDB(
      asPath.slice(1),
      'append_to_response=account_states,external_ids,images,keywords,videos,recommendations,reviews,reviews'
    );
    return {
      movie
    };
  } catch (error) {
    console.log(error);
  }
};

export default MovieProfile;
